import React, { ReactNode } from 'react';
import { Grid, makeStyles, Typography, Theme } from '@material-ui/core';

interface Props {
  title: string;
  description: string;
  children: ReactNode;
}

const useStyles = makeStyles<Theme>(({ spacing }) => ({
  section: {
    padding: `0 0 ${spacing(15)}px`,
  },
  header: {
    marginBottom: spacing(3),
  },
  title: {
    fontWeight: 'bold',
  },
  whiteSpacePre: {
    whiteSpace: 'pre-line',
  },
}));

const DescriptionSection = ({ title, description, children }: Props) => {
  const style = useStyles();
  return (
    <Grid component="section" className={style.section} container>
      <Grid item xs={12} className={style.header}>
        <Grid xs={12} item>
          <Typography
            variant="h5"
            component="h2"
            gutterBottom={true}
            className={`${style.whiteSpacePre} ${style.title}`}
          >
            {title}
          </Typography>
        </Grid>
        <Grid>
          <Typography variant="body2" color="textSecondary" className={style.whiteSpacePre}>
            {description}
          </Typography>
        </Grid>
      </Grid>
      <div>{children}</div>
    </Grid>
  );
};

export default DescriptionSection;
